(function(factory) {
    if (typeof define === 'function' && define.amd) {
        // AMD. Register as an anonymous module.
        define(['jquery'], factory);
    } else if (typeof module === 'object' && module.exports) {
        // Node/CommonJS
        module.exports = factory(require('jquery'));
    } else {
        // Browser globals
        factory(jQuery);
    }
}(function($) {

    $.fn.scrollToTop = function() {

        var defaults = {
            element: '.scrolltop',
            pageElement: 'html, body',
            activeClass: 'active',
            scrollHeight: 100,
            transitionDuration: 500,
            easing: 'linear',
        }

        var settings = $.extend({}, defaults, $.fn.scrollToTop.defaults);

        var $window = $(window);
        var element = $(settings.element);

        var _scroll = function() {
            var page = settings.pageElement;

            $(page).animate({
                scrollTop: 0
            }, settings.transitionDuration)
        }

        var _hide = function(target) {
            target.removeClass(settings.activeClass);
        }

        var _show = function(target) {
            target.addClass(settings.activeClass);
        }

        if ($window.scrollTop() >= settings.scrollHeight) {
            _show(element)
        }

        $window.scroll(function() {
            var scrollTop = $(this).scrollTop();

            if (scrollTop >= settings.scrollHeight) {
                _show(element)
            } else {
                _hide(element)
            }
        })

        element.on('click', function() {
            _scroll()
        })
    }
}));
