(function(factory) {
    if (typeof define === 'function' && define.amd) {
        // AMD. Register as an anonymous module.
        define(['jquery'], factory);
    } else if (typeof module === 'object' && module.exports) {
        // Node/CommonJS
        module.exports = factory(require('jquery'));
    } else {
        // Browser globals
        factory(jQuery);
    }
}(function($) {

    $.fn.headerDropdown = function() {
        var defaults = {
            element: {
                main: '.header-dropdown.header-dropdown-click',
                toggle: '.header-dropdown-toggle',
                menu: '.header-dropdown-menu',
            },
            activeClass: 'active',
        }

        var settings = $.extend({}, defaults, $.fn.headerDropdown.defaults);

        var _show = function(target) {
            target.addClass(settings.activeClass).siblings().filter(settings.element.main).removeClass(settings.activeClass);
        }

        var _hide = function(target) {
            target.removeClass(settings.activeClass);
        }

        var element = $(settings.element.main);

        element.each(function() {
            var toggle = $(this).find(settings.element.toggle);

            toggle.on('click', function() {
                var target = $(this).parent(settings.element.main);

                if (target.hasClass(settings.activeClass)) {
                    _hide(target);
                } else {
                    _show(target);
                }
            })
        })
    }

}));
