(function(factory) {
    if (typeof define === 'function' && define.amd) {
        // AMD. Register as an anonymous module.
        define(['jquery'], factory);
    } else if (typeof module === 'object' && module.exports) {
        // Node/CommonJS
        module.exports = factory(require('jquery'));
    } else {
        // Browser globals
        factory(jQuery);
    }
}(function($) {

    $.fn.product = function(action){
        var defaults = {
            element: {
                main: '.product.product-hover-offset',
                menu: '.product-menu',
                hover: '.product-hover-offset',
                offset: '.product-offset',
                imgPrimary: '.product-image-primary',
                imgSecondary: '.product-image-secondary'
            },
            class: {
                horizontal: 'product-horizontal',
            },
            data: {
                offset: 'product-offset',
                hoverActive: 'product-hover-active'
            }
        }

        var events = [
            {
                event: 'hover',
                action: function(target){
                    target.filter(settings.element.hover).each(function() {
                        _hoverInit($(this))
                    })
                }
            },
            {
                event: 'unHover',
                action: function(target){
                    target.filter(settings.element.hover).each(function() {
                        _hoverDestroy($(this), true)
                    })
                }
            },
            {
                event: 'horizontal',
                action: function(target){
                    target.each(function(){
                        _horizontal($(this));
                    })
                }
            },
            {
                event: 'vertical',
                action: function(target){
                    target.each(function(){
                        _vertical($(this));
                    })
                }
            }
        ]

        var settings = $.extend({}, defaults, $.fn.product.defaults);

        var _pixelMaker = function(val, min) {
            var prepend = '-';
            var append = 'px';

            val = val + append;
            if (min) {
                val = prepend + val;
            }

            return val;
        }

        var _hoverInit = function(target) {
            var menu = target.find(settings.element.menu);
            var height = target.find(settings.element.offset).outerHeight();

            target.data(settings.data.offset, height);
            target.data(settings.data.hoverActive, true);

            if (!target.hasClass(settings.class.horizontal)) {
                menu.css('margin-bottom', _pixelMaker(height, true));
                menu.css('bottom', _pixelMaker(0, false));

                target.hover(function() {
                    _show($(this));
                }, function() {
                    _hide($(this));
                });
            }
        }

        var _hoverDestroy = function(target, dataActive){
            var menu = target.find(settings.element.menu);
            menu.css('margin-bottom', _pixelMaker(0, true));
            menu.css('bottom', _pixelMaker(0, false));
            target.off();

            if(dataActive){
                target.data(settings.data.hoverActive, false);
            }
        }

        var _horizontal = function(target){
            var hoverActive = target.data(settings.data.hoverActive);

            target.addClass(settings.class.horizontal);
            if(hoverActive){
                _hoverDestroy(target, false)
            }
        }

        var _vertical = function(target){
            var hoverActive = target.data(settings.data.hoverActive);

            target.removeClass(settings.class.horizontal);
            if(hoverActive){
                _hoverInit(target)
            }
        }

        var _hide = function(target) {
            var menu = target.find(settings.element.menu);

            menu.css('bottom', _pixelMaker(0, false));
        }

        var _show = function(target) {
            var menu = target.find(settings.element.menu);
            var offset = target.data(settings.data.offset);;

            menu.css('bottom', _pixelMaker(offset, false));
        }

        var element = $(this);

        if(typeof action == 'string'){
            events.forEach(function(arr){
                if(action == arr.event){
                    arr.action(element)
                }
            })
        }
        
        return this
    }

}));
