(function(factory) {
    if (typeof define === 'function' && define.amd) {
        // AMD. Register as an anonymous module.
        define(['jquery'], factory);
    } else if (typeof module === 'object' && module.exports) {
        // Node/CommonJS
        module.exports = factory(require('jquery'));
    } else {
        // Browser globals
        factory(jQuery);
    }
}(function($) {
    $.fn.grid = function(action, callback) {
        var defaults = {
            element: '.grid',
            prefixClass: 'grid-fix-',
            colsTotal: 6,
            dataGridTotal: 'grid-total-cols',
            dataCallback: {
                onChange: 'grid-on-change',
                onDetach: 'grid-on-detach'
            }
        }

        var events = [{
            event: 'detach',
            action: function(target) {
                _detach(target)
            }
        }]

        var settings = $.extend({}, defaults, $.fn.grid.defaults);

        var _removeAllClass = function(target) {
            for (var i = 1; i <= settings.colsTotal; i++) {
                var checkClass = settings.prefixClass + i;
                $(target).removeClass(checkClass);
            }

            return true
        }

        var _detach = function(target) {
            target.filter(settings.element).each(function() {
                _removeAllClass($(this));
                $(this).data(settings.dataGridTotal, null);

                var callback = $(this).data(settings.dataCallback.onDetach);
                if (typeof callback == 'function') {
                    callback($(this))
                }
            })
        }

        var _gridChange = function(target, col) {
            target.filter(settings.element).each(function() {
                if (col > 0 && col <= settings.colsTotal) {
                    var finalClass = settings.prefixClass + col;

                    if (_removeAllClass($(this))) {
                        $(this).addClass(finalClass);
                        $(this).data(settings.dataGridTotal, col);

                        var callback = $(this).data(settings.dataCallback.onChange);
                        if (typeof callback == 'function') {
                            callback($(this), col)
                        }
                    }
                }
            })
        }

        var element = $(this);

        if (typeof action == 'number') {

            var col = action;
            _gridChange(element, col);


        } else if (typeof action == 'string') {
            if (typeof callback == 'function') {
                element.filter(settings.element).each(function() {
                    var callbackEvent = settings.dataCallback[action];

                    if (callbackEvent != undefined) {
                        $(this).data(callbackEvent, callback)
                    }
                })
            } else {
                events.forEach(function(arr) {
                    if (action == arr.event) {
                        arr.action(element)
                    }
                })
            }
        }

        return this
    }

}));
