(function(factory) {
    if (typeof define === 'function' && define.amd) {
        // AMD. Register as an anonymous module.
        define(['jquery'], factory);
    } else if (typeof module === 'object' && module.exports) {
        // Node/CommonJS
        module.exports = factory(require('jquery'));
    } else {
        // Browser globals
        factory(jQuery);
    }
}(function($) {

    $.fn.comparation = function() {

        var defaults = {
            element: {
                main: '.comparation',
                header: '.comparation-header',
                body: '.comparation-body',
                group: '.comparation-group',
                item: '.comparation-item'
            },
            data: {
                headerItemHeight: 'comparation-header-item-height',
                groupItemHeight: 'comparation-group-item-height',
                collapseHeight: 'comparation-collapse-item-height',
                finalHeight: 'comparation-final-item-height',
                minItem: 'comparation-min-item'
            }
        }

        var settings = $.extend({}, defaults, $.fn.comparation.defaults);

        var element = $(this);

        var finalHeight = [];
        var minItem = null;

        function _get(target) {
            var headerHeight = [];
            var groupHeight = [];
            var collapseHeight = [];
            var bufferArray = [];
            var progress = true;

            finalHeight = [];
            minItem = null;


            // Get height data from compare item in header and append to collapseHeight array
            var headerItem = target.find(settings.element.header).find(settings.element.item);

            if (headerItem.length != 0 && progress) {
                headerItem.each(function() {
                    $(this).css('height', 'auto');
                    headerHeight.push($(this).outerHeight());
                });
                target.data(settings.data.headerItemHeight, headerHeight);
                headerHeight = [];
            } else {
                progress = false;
            }

            // Get height data from compare item in body and append to collapseHeight array
            var group = target.find(settings.element.body).find(settings.element.group);

            if (group.length != 0 && progress) {
                group.each(function() {
                    var groupItem = $(this).find(settings.element.item);
                    if (groupItem.length != 0) {
                        groupItem.each(function() {
                            $(this).css('height', 'auto');
                            bufferArray.push($(this).outerHeight());
                        });
                        groupHeight.push(bufferArray);
                        bufferArray = [];
                    }
                });
                target.data(settings.data.groupItemHeight, groupHeight);
                groupHeight = [];
            } else {
                progress = false;
            }

            // Collapse all height data from all compare items and define min item
            if (progress) {
                collapseHeight = target.data(settings.data.groupItemHeight);
                collapseHeight.push(target.data(settings.data.headerItemHeight))

                collapseHeight.forEach(function(arr) {
                    if (minItem == null) {
                        minItem = arr.length
                    } else {
                        if (arr.length < minItem) {
                            minItem = arr.length;
                        }
                    }
                })
                target.data(settings.data.minItem, minItem);

                var bufferArray = [];
                var bufferArrayDimension = [];

                collapseHeight.forEach(function(arr) {
                    for (var i = 0; i < minItem; i++) {
                        bufferArray.push(arr[i]);
                    }
                    bufferArrayDimension.push(bufferArray);
                    bufferArray = [];
                })
                target.data(settings.data.collapseHeight, bufferArrayDimension);
                minItem = null;
                collapseHeight = [];
            } else {
                progress = false;
            }

            return progress;
        }

        function _prepare(target) {

            var bufferArray = [];
            var firstArray = true;
            var progress = true;

            collapseHeight = target.data(settings.data.collapseHeight);
            minItem = target.data(settings.data.minItem);

            if (collapseHeight && minItem && progress) {
                collapseHeight.forEach(function(arr) {
                    for (var i = 0; i < minItem; i++) {
                        if (firstArray) {
                            bufferArray.push(arr[i]);
                        } else {
                            if (arr[i] > bufferArray[i]) {
                                bufferArray[i] = arr[i];
                            }
                        }
                    }
                    firstArray = false;
                })

                target.data(settings.data.finalHeight, bufferArray);
                bufferArray = [];
            } else {
                progress = false;
            }

            return progress;
        }

        function _insert(target) {
            finalHeight = target.data(settings.data.finalHeight);
            minItem = target.data(settings.data.minItem);

            if (minItem && finalHeight) {
                for (var i = 0; i < minItem; i++) {
                    var px = finalHeight[i] + 'px';
                    target.find(settings.element.header).find(settings.element.item).eq(i).css('height', px);
                }

                target.find(settings.element.body).find(settings.element.group).each(function() {
                    for (var i = 0; i < minItem; i++) {
                        var px = finalHeight[i] + 'px';

                        $(this).find(settings.element.item).eq(i).css('height', px);
                    }
                });
            }

        }

        element.filter(settings.element.main).each(function() {
            var $this = $(this);

            if (_get($this)) {
                if (_prepare($this)) {
                    _insert($this)
                }
            }
        })
    }
}));
