(function(factory) {
    if (typeof define === 'function' && define.amd) {
        define(['jquery'], factory);
    } else if (typeof module === 'object' && module.exports) {
        module.exports = factory(require('jquery'));
    } else {
        factory(jQuery);
    }
}(function($) {

    $.fn.accordian = function(action, callback) {

        var defaults = {
            element: {
                main: '.accordian',
                head: '.accordian-head',
                body: '.accordian-body',
            },
            transitionDuration: 200,
            easing: 'swing',
            data: {
                height: 'accordian-height',
                active: 'accordian-active',
            },
            activeClass: 'active',
            dataCallback: {
                onShow: 'accordian-on-show',
                onHide: 'accordian-on-hide',
                onInit: 'accordian-on-init',
                onRecalc: 'accordian-on-recalc',
            }
        }

        var events = [{
                event: 'init',
                action: function(target) {
                    _init(target);

                    target.filter(settings.element.main).each(function() {
                        var callback = $(this).data(settings.dataCallback.onInit);

                        if (typeof callback == 'function') {
                            callback($(this))
                        }
                    })
                }
            },
            {
                event: 'show',
                action: function(target) {
                    _show(target, true);
                }
            },
            {
                event: 'hide',
                action: function(target) {
                    _hide(target, true);
                }
            },
            {
                event: 'recalc',
                action: function(target) {
                    target.filter(settings.element.main).each(function() {
                        var active = $(this).data(settings.data.active)
                        var callback = $(this).data(settings.dataCallback.onRecalc);

                        _calc($(this));
                        if (active) {
                            _show($(this))
                        }
                        if (typeof callback == 'function') {
                            callback($(this))
                        }
                    })
                }
            },
        ]

        var settings = $.extend({}, defaults, $.fn.accordian.defaults);

        var _calc = function(target) {
            var body = target.find(settings.element.body);
            var height = body.outerHeight();

            target.data(settings.data.height, height);
        }

        var _init = function(target) {
            target.filter(settings.element.main).each(function() {
                var body = $(this).find(settings.element.body);
                _calc($(this));

                if ($(this).hasClass(settings.activeClass)) {
                    _show($(this), false);
                } else {
                    _hide($(this), false);
                }

                _listener($(this))
            })

            return true;
        }

        var _listener = function(target) {
            var head = target.find(settings.element.head);

            head.on('click', function() {
                var parent = $(this).parent(settings.element.main);
                var active = parent.data(settings.data.active);

                if (active) {
                    _hide(parent, true);
                } else {
                    _show(parent, true);
                }
            })
        }

        var _show = function(target, callbackActive) {
            var body = target.find(settings.element.body);
            var height = target.data(settings.data.height);
            var easing = settings.easing;
            var duration = settings.transitionDuration;

            target.data(settings.data.active, true);
            body.animate({
                height: height,
            }, {
                duration: duration,
                easing: easing,
                complete: function() {
                    var parent = $(this).parent(settings.element.main);

                    $(this).css('height', 'auto');
                    $(this).css('overflow', 'auto');
                    parent.addClass(settings.activeClass);

                    if (callbackActive) {
                        var callback = parent.data(settings.dataCallback.onShow);

                        if (typeof callback == 'function') {
                            callback(parent)
                        }
                    }
                }
            })
        }

        var _hide = function(target, callbackActive) {
            var body = target.find(settings.element.body);
            var easing = settings.easing;
            var duration = settings.transitionDuration;

            target.data(settings.data.active, false);
            body.animate({
                height: 0,
            }, {
                duration: duration,
                easing: easing,
                complete: function() {
                    var parent = $(this).parent(settings.element.main);

                    $(this).css('overflow', 'hidden');
                    parent.removeClass(settings.activeClass);

                    if (callbackActive) {
                        var callback = parent.data(settings.dataCallback.onHide);
                        if (typeof callback == 'function') {
                            callback(parent)
                        }
                    }
                }
            })
        }

        var element = $(this);

        if (typeof action == 'string') {
            if (typeof callback == 'function') {
                element.filter(settings.element.main).each(function() {
                    var callbackEvent = settings.dataCallback[action];

                    if (callbackEvent != undefined) {
                        $(this).data(callbackEvent, callback)
                    }
                })
            } else {
                events.forEach(function(arr) {
                    if (action == arr.event) {
                        arr.action(element)
                    }
                })
            }
        } else if (action == undefined) {
            events[0].action(element)
        }

        return this
    }
}));
