(function(factory) {
    if (typeof define === 'function' && define.amd) {
        // AMD. Register as an anonymous module.
        define(['jquery'], factory);
    } else if (typeof module === 'object' && module.exports) {
        // Node/CommonJS
        module.exports = factory(require('jquery'));
    } else {
        // Browser globals
        factory(jQuery);
    }
}(function($) {
    $.fn.sidemenu = function(action, callback) {

        var defaults = {
            element: {
                main: '.sidemenu',
                backdrop: '.sidemenu-backdrop',
                page: 'body',
            },
            class: {
                body: 'sidemenu-open',
                    active: 'active'
            },
            transitionDuration: 200,
            easing: 'linear',
            data: {
                height: 'sidemenu-height',
                active: 'sidemenu-active',
                target: 'sidemenu-target'
            },
            dataCallback: {
                onShow: 'sidemenu-on-show',
                onHide: 'sidemenu-on-hide',
            }
        }

        var events = [{
                event: 'show',
                action: function(target) {
                    _show(target);
                }
            },
            {
                event: 'hide',
                action: function(target) {
                    _hide(target);
                }
            },
            {
                event: 'toggle',
                action: function(target) {
                    if ($(settings.element.page).hasClass(settings.class.body) && $(this).hasClass(settings.class.active)) {
                        _hide(target);
                    } else {
                        _show(target);
                    }
                }
            },
        ]

        var settings = $.extend({}, defaults, $.fn.sidemenu.defaults);

        function _backdropShow() {
            var backdrop = settings.element.backdrop;
            var page = settings.element.page;
            var html = '<div class="' + backdrop.substr(1) + '" style="opacity: 0"></div>';
            var easing = settings.easing;
            var duration = settings.transitionDuration;

            $(page).prepend(html);

            $(backdrop).animate({
                opacity: 1,
            }, {
                duration: duration,
                easing: easing,
                complete: function() {
                    $(this).on('click', function() {
                        _hide($(settings.element.main + '.' + settings.class.active))
                    })
                }
            })
        }

        function _backdropHide() {
            var backdrop = settings.element.backdrop;
            var page = settings.element.page;
            var easing = settings.easing;
            var duration = settings.transitionDuration;

            $(backdrop).animate({
                opacity: 0,
            }, {
                duration: duration,
                easing: easing,
                complete: function() {
                    $(this).remove()
                }
            }).off()
        }

        function _show(target) {
            target.addClass(settings.class.active);
            $(settings.element.page).addClass(settings.class.body);

            _backdropShow();
            var callback = target.data(settings.dataCallback.onShow);
            if (typeof callback == 'function') {
                callback(target)
            }
        }

        function _hide(target) {
            target.removeClass(settings.class.active);
            $(settings.element.page).removeClass(settings.class.body);
            _backdropHide();

            var callback = target.data(settings.dataCallback.onHide);
            if (typeof callback == 'function') {
                callback(target)
            }
        }

        var element = $(this);

        if (typeof action == 'string') {
            if (typeof callback == 'function') {
                element.filter(settings.element.main).each(function() {
                    var callbackEvent = settings.dataCallback[action];

                    if (callbackEvent != undefined) {
                        $(this).data(callbackEvent, callback)
                    }
                })
            } else {
                events.forEach(function(arr) {
                    if (action == arr.event) {
                        arr.action(element)
                    }
                })
            }
        }

        return this
    }

}));
